<script>
  import { forceSimulation, forceCollide, forceX, forceY } from "d3-force";
  import { onMount, getContext } from "svelte";

  // Access the context using the 'LayerCake' keyword
  // Grab some helpful functions
  const { data, xGet, yGet, xScale, yScale } = getContext("LayerCake");

  let simData = [];
  const simulation = forceSimulation().stop();

  const runSim = () => {
    simData = [...$data];
    simulation
      .nodes(simData)
      .force("collide", forceCollide($xScale.domain()[1] * 0.025))
      .force("x", forceX($xScale.domain()[1] / 2))
      .force("y", forceY($yScale.domain()[1] / 2))
      .on("tick", () => {
        simData = [...simData];
        // console.log(simData[0].x);
      });

    for (var i = 0; i < 300; i++) {
      simulation.tick();
    }
    // .restart()
  };

  // $: console.log($xScale.range(), $yScale.range());

  $: $data, runSim();

  onMount(() => {});

  // $: console.log($data);
</script>

{#each simData as d}
  <div style="left: {$xGet(d)}%; top: {$yGet(d)}%;"></div>
{/each}

<style>
  div {
    position: absolute;
    width: 10%;
    height: 5%;
    background: purple;
    border-radius: 50%;
    border: 1px solid black;
  }
</style>
