<script>
  import { LayerCake, Html } from "layercake";
  import Dots from "./Scatter.Dots.svelte";
  import Labels from "./Scatter.Labels.svelte";
  import bands from "../data/bands.csv";

  const data = bands.map((d) => ({
    ...d,
    followers: +d.followers,
  }));

  const points = [
    { x: 0, y: 1, text: "A" },
    { x: 10, y: 5, text: "B" },
    { x: 15, y: 10, text: "C" },
  ];

  const fixedAspectRatio = 1 / 2;
</script>

<div class="chart-container">
  <figure style="padding-bottom: {100 / fixedAspectRatio}%">
    <LayerCake
      data="{points}"
      x="x"
      y="y"
      position="absolute"
      ssr="{true}"
      percentRange="{true}"
    >
      <Html>
        <Dots r="{4}" scaled="{false}" />
      </Html>
      <Html>
        <Labels />
      </Html>
    </LayerCake>
  </figure>
</div>

<style>
  .chart-container {
    width: 50%;
    max-width: 480px;
    margin: 0 auto;
  }

  figure {
    position: relative;
    width: 100%;
    background: pink;
  }
